import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import "react-loading-skeleton/dist/skeleton.css";
import MediathequeService from "src/modules/mediatheque/MediathequeService";

// COMPONENTS
import Skeleton from "react-loading-skeleton";
import VerticalPlayer from "./VerticalPlayer";
import Breadcrumb from "../../shared/Breadcrumb";
import { i18n } from "../../../i18n";
import Pagination from "src/view/shared/table/Pagination";

export default function SpecificEntityList() {
  const params = useParams();
  let param = params.entity_type;

  switch (param) {
    case "photos":
      param = "autres";
      break;
    case "documentaires":
      param = "documentaire";
      break;
    case "podcasts":
      param = "podcast";
      break;

    default:
      break;
  }
  const [entities, setEntities] = useState([]);
  const [entityIsLoading, setEntityIsLoading] = useState(true);
  const [data, setData] = useState(new Array());
  const [count, setCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(12);
  const [loading, setLoading] = useState(false);

  const getData = async (offset) => {
    try {
      setLoading(true);
      let result = await MediathequeService.list(
        { type: param },
        "",
        pageSize,
        offset
      );
      setData(result.rows);
      setCount(result.count);
      setLoading(false);
    } catch (error) {
      console.log("err", error);
      setLoading(false);
    }
  };
  useEffect(() => {
    getData(0);
  }, [params.entity_type]);

  // const fetchTypeEntities = async () => {
  //   setEntities([]);
  //   await MediathequeService.list({ type: param }, "", 12, 0).then((value) => {
  //     value.rows?.map((entry) => {
  //       setEntities((entities) => entities.concat(entry));
  //     });
  //     setEntityIsLoading(false);
  //   });
  // };

  // useEffect(() => {
  //   fetchTypeEntities();
  // }, [params.entity_type]);

  return (
    <section>
      <section className="wideContent">
        <Breadcrumb
          title={i18n(`entities.mediatique.enumerators.type.${param}`)}
          items={[
            [i18n("app.title"), "/"],
            [i18n("entities.mediatique.label"), "/Mediatheque"],
          ]}
        />
        {loading ? (
          <MainLoadingLayout>
            <Skeleton height={300} width={270} />
            <Skeleton height={300} width={270} />
            <Skeleton height={300} width={270} />
            <Skeleton height={300} width={270} />
          </MainLoadingLayout>
        ) : (
          <>
            <MainLayout>
              {data?.map((ent, index) => {
                return (
                  <VerticalPlayer dataSource={ent} key={index} />
                  // END OF MAP RETURN
                );
              })}
            </MainLayout>
            <Pagination
              onChange={(e) => {
                const offNumber = (e.current - 1) * e.pageSize;
                getData(offNumber);
                setCurrentPage(e.current);
              }}
              disabled={loading}
              pagination={{
                current: currentPage,
                pageSize: pageSize,
                showSizeChanger: true,
                total: count,
              }}
            />
          </>
        )}
      </section>
    </section>
  );
}

const MainLoadingLayout = styled.section`
  width: 100%;
  margin: 2rem 0;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  .react-loading-skeleton {
    width: 100%;
    height: 200px;
  }
`;

const MainLayout = styled.section`
  width: 100%;
  margin: 2rem 0;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(370px, 1fr));
  grid-gap: 1rem;

  @media (max-width: 767px) {
    padding: 0 0.5rem;
  }
`;
